"use strict";

/**
 * @author Ryan Heaton
 */
function FIDataList () {

  /**
   * @name FIDataList#data
   * @type BaseFinancialInstitutionData[]
   * @access private
   */
  this.data = null;
}





FIDataList.prototype.getData = function() {
  return this.data;
};


FIDataList.prototype.setData = function(/*Collection<BaseFinancialInstitutionData>*/ data) {
  this.data = data;
};




module.exports = FIDataList;
